.btnSecond {
    font-size: 14px;
    opacity: 30%;
    background-repeat: no-repeat;
    background-position: 26px;
    background-color: transparent;
    text-align: left;
    padding: 0 26px 0 52px;
    width: 300px;
    height: 34px;
    border: 0;
    letter-spacing: 1px;
    border-radius: 6px;
    transition: 0.3s ease;
    margin-bottom: 16px;
    margin-top: 2px;
    margin-left: 20px;
}

.btnSecond.light {
    color: #5e5e5e;
}

.btnSecond.dark {
    color: #aab4bd;
}

.btnSecond:hover {
    opacity: 70%;
    cursor: pointer;
}

.autoFill.light {
    background-image: url('/src/assets/icons/light_theme/auto_fill.svg');
}

.deleteAll.light {
    background-image: url('/src/assets/icons/light_theme/delete_all.svg');
}

.autoFill.dark {
    background-image: url('/src/assets/icons/dark_theme/auto_fill_light.svg');
}

.deleteAll.dark {
    background-image: url('/src/assets/icons/dark_theme/delete_all_light.svg');
}

.containerButtons {
    display: flex;
    justify-content: left;
}